import React, {useState, useRef} from "react";
import {Link, useNavigate} from "react-router-dom";
import {register, checkUsernameAvailability, checkEmailAvailability} from "../api";
import "./styles.css";

const validate = {
    username: (username: string) => {
        if (!username.trim()) return " .";
        if (username.length < 4) return " 4  .";
        return "";
    },
    password: (password: string, confirm: string) => {
        if (!password) return " .";
        if (password.length < 8) return " 8  .";
        if (password !== confirm) return "  .";
        return "";
    },
    email: (email: string) => {
        if (!email.trim()) return " .";
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email) ? "" : "   .";
    },
    fullName: (name: string) => (!name.trim() ? " ." : ""),
    terms: (agreed: boolean) => (!agreed ? " ." : ""),
};

const Signup = () => {
    const navigate = useNavigate();

    const [form, setForm] = useState({
        username: "",
        password: "",
        passwordConfirm: "",
        email: "",
        fullName: "",
        nickname: "",
        agreeToTerms: false,
    });

    const [errors, setErrors] = useState({
        username: "",
        password: "",
        email: "",
        fullName: "",
        terms: "",
    });

    const [messages, setMessages] = useState({
        error: "",
        success: "",
    });

    const [isLoading, setIsLoading] = useState(false);

    const handleChange = (field: string, value: any) => {
        setForm({...form, [field]: value});
    };

    const checkUsername = async () => {
        const error = validate.username(form.username);
        if (error) {
            setErrors(prev => ({...prev, username: error}));
            return false;
        }
        try {
            const res = await checkUsernameAvailability(form.username);
            if (!res.available) {
                setErrors(prev => ({...prev, username: res.message || "  ."}));
                return false;
            }
            setErrors(prev => ({...prev, username: ""}));
            return true;
        } catch (error) {
            console.error("   :", error);
            setErrors(prev => ({...prev, username: "    ."}));
            return false;
        }
    };

    const checkEmail = async () => {
        const error = validate.email(form.email);
        if (error) {
            setErrors(prev => ({...prev, email: error}));
            return false;
        }
        try {
            const res = await checkEmailAvailability(form.email);
            if (!res.available) {
                setErrors(prev => ({...prev, email: res.message || "   ."}));
                return false;
            }
            setErrors(prev => ({...prev, email: ""}));
            return true;
        } catch (error) {
            console.error("   :", error);
            setErrors(prev => ({...prev, email: "   ."}));
            return false;
        }
    };

    const validateForm = () => {
        const passwordErr = validate.password(form.password, form.passwordConfirm);
        const fullNameErr = validate.fullName(form.fullName);
        const termsErr = validate.terms(form.agreeToTerms);

        setErrors(prev => ({
            ...prev,
            password: passwordErr,
            fullName: fullNameErr,
            terms: termsErr,
        }));

        return !(passwordErr || fullNameErr || termsErr);
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!validateForm()) return;

        const isUsernameValid = await checkUsername();
        const isEmailValid = await checkEmail();
        if (!isUsernameValid || !isEmailValid) return;

        try {
            setIsLoading(true);
            setMessages({error: "", success: ""});

            //   
            const requestData = {
                username: form.username,
                password: form.password,
                email: form.email,
                fullName: form.fullName,
                nickname: form.nickname || undefined,
                agreeToTerms: form.agreeToTerms,
            };
            
            console.log("  :", requestData);

            const res = await register(requestData);
            console.log(" :", res);

            if (res.success) {
                //      
                if (res.username) {
                    localStorage.setItem('username', res.username);
                    console.log(' :    ', res.username);
                } else {
                    //         
                    localStorage.setItem('username', form.username);
                    console.log(' :     ', form.username);
                }
                
                //   
                if (form.nickname) {
                    localStorage.setItem('userNickname', form.nickname);
                }
                
                setMessages({success: " .    .", error: ""});
                setTimeout(() => navigate(`/verify-email?email=${encodeURIComponent(form.email)}`), 1500);
            } else {
                setMessages({error: res.message || " .", success: ""});
            }
        } catch (err) {
            console.error(" :", err);
            setMessages({error: "    .", success: ""});
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="signup-container">
            <div className="signup-card">
                <div className="signup-nav">
                    <Link to="/main"></Link>
                    <Link to="/login"></Link>
                </div>

                <h2 className="signup-title"></h2>

                {messages.error && <div className="signup-message error">{messages.error}</div>}
                {messages.success && <div className="signup-message success">{messages.success}</div>}

                <form onSubmit={handleSubmit} className="signup-form">
                    <div className="signup-form-field">
                        <label htmlFor="username">
                             <span className="required">*</span>
                        </label>
                        <input
                            id="username"
                            type="text"
                            value={form.username}
                            onChange={(e) => handleChange("username", e.target.value)}
                            onBlur={checkUsername}
                            placeholder="4  "
                        />
                        {errors.username && <div className="field-error">{errors.username}</div>}
                    </div>

                    <div className="signup-form-field">
                        <label htmlFor="password">
                             <span className="required">*</span>
                        </label>
                        <input
                            id="password"
                            type="password"
                            value={form.password}
                            onChange={(e) => handleChange("password", e.target.value)}
                            placeholder="8  "
                        />
                        {errors.password && <div className="field-error">{errors.password}</div>}
                    </div>

                    <div className="signup-form-field">
                        <label htmlFor="passwordConfirm">
                              <span className="required">*</span>
                        </label>
                        <input
                            id="passwordConfirm"
                            type="password"
                            value={form.passwordConfirm}
                            onChange={(e) => handleChange("passwordConfirm", e.target.value)}
                            placeholder="  "
                        />
                    </div>

                    <div className="signup-form-field">
                        <label htmlFor="email">
                             <span className="required">*</span>
                        </label>
                        <input
                            id="email"
                            type="email"
                            value={form.email}
                            onChange={(e) => handleChange("email", e.target.value)}
                            onBlur={checkEmail}
                            placeholder="example@email.com"
                        />
                        {errors.email && <div className="field-error">{errors.email}</div>}
                    </div>

                    <div className="signup-form-field">
                        <label htmlFor="fullName">
                             <span className="required">*</span>
                        </label>
                        <input
                            id="fullName"
                            type="text"
                            value={form.fullName}
                            onChange={(e) => handleChange("fullName", e.target.value)}
                            placeholder=" "
                        />
                        {errors.fullName && <div className="field-error">{errors.fullName}</div>}
                    </div>

                    <div className="signup-form-field">
                        <label htmlFor="nickname"> ()</label>
                        <input
                            id="nickname"
                            type="text"
                            value={form.nickname}
                            onChange={(e) => handleChange("nickname", e.target.value)}
                            placeholder="   "
                        />
                    </div>

                    <div className="terms">
                        <input
                            id="agreeToTerms"
                            type="checkbox"
                            checked={form.agreeToTerms}
                            onChange={(e) => handleChange("agreeToTerms", e.target.checked)}
                        />
                        <label htmlFor="agreeToTerms">
                            <span className="required">*</span>     .
                        </label>
                        {errors.terms && <div className="field-error">{errors.terms}</div>}
                    </div>

                    <button
                        type="submit"
                        disabled={isLoading}
                    >
                        {isLoading ? " ..." : ""}
                    </button>
                </form>
            </div>
        </div>
    );
};

export default Signup;
