import apiClient from "../../../api/apiClient";
import { RegisterRequest } from "../model";

// API   (   )
const AUTH_BASE_URL = "/api/auth";
const REGISTER_BASE_URL = "/api/register";

/**
 *  API 
 * @param data  
 * @returns  
 */
export const register = async (data: RegisterRequest) => {
    try {
        console.log(" API :", `${REGISTER_BASE_URL}/signup`, data);
        
        const response = await apiClient.post(`${REGISTER_BASE_URL}/signup`, data);
        console.log(" API :", response.data);
        return response.data;
    } catch (error: any) {
        console.error(" :", error);
        
        //    
        if (error.response) {
            console.error("  :", error.response.data);
            console.error("  :", error.response.status);
            console.error(" :", error.response.headers);
            
            //     
            if (error.response.data && error.response.data.message) {
                return {
                    success: false,
                    message: ` : ${error.response.data.message}`
                };
            }
        }
        
        return {
            success: false,
            message: "    .    ."
        };
    }
};

/**
 *   
 * @param username  
 * @returns   
 */
export const checkUsernameAvailability = async (username: string) => {
    try {
        // UsernameAvailabilityRequest     
        const requestData = { username };
        console.log("   :", requestData, `${REGISTER_BASE_URL}/check-username`);
        
        const response = await apiClient.post(`${REGISTER_BASE_URL}/check-username`, requestData);
        console.log("   :", response.data);
        return response.data;
    } catch (error) {
        console.error("   :", error);
        return {
            available: false,
            message: "    ."
        };
    }
};

