import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, useLocation } from 'react-router-dom';
import './EmailVerification.css';

// API  URL 
const BASE_URL = 'http://localhost:8080/api';

interface EmailVerificationProps {
  onSuccess?: () => void;
}

const EmailVerification: React.FC<EmailVerificationProps> = ({ onSuccess }) => {
  const [email, setEmail] = useState('');
  const [verificationCode, setVerificationCode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState<{ text: string; type: 'error' | 'success' | 'info' } | null>(null);
  const [codeRequested, setCodeRequested] = useState(false);
  
  const navigate = useNavigate();
  const location = useLocation();
  
  // URL   
  React.useEffect(() => {
    const params = new URLSearchParams(location.search);
    const emailParam = params.get('email');
    if (emailParam) {
      setEmail(emailParam);
    }
  }, [location]);

  //   
  const requestVerificationCode = async () => {
    if (!email.trim()) {
      setMessage({ text: ' .', type: 'error' });
      return;
    }

    try {
      setIsLoading(true);
      setMessage({ text: '   ...', type: 'info' });

      const response = await axios.post(`${BASE_URL}/register/resend-verification`, { email });

      if (response.data.success) {
        setMessage({ text: '   .', type: 'success' });
        setCodeRequested(true);
      } else {
        setMessage({ text: response.data.message || '   ', type: 'error' });
      }
    } catch (error) {
      console.error('   :', error);
      setMessage({ text: '    .', type: 'error' });
    } finally {
      setIsLoading(false);
    }
  };

  //   
  const verifyCode = async () => {
    if (!email.trim() || !verificationCode.trim()) {
      setMessage({ text: '    .', type: 'error' });
      return;
    }

    try {
      setIsLoading(true);
      setMessage({ text: '  ...', type: 'info' });

      const response = await axios.post(`${BASE_URL}/auth/verify-code`, {
        email,
        code: verificationCode
      });

      if (response.data.success) {
        setMessage({ text: '  .', type: 'success' });
        //    
        if (onSuccess) {
          setTimeout(() => {
            onSuccess();
          }, 1500);
        } else {
          //      
          setTimeout(() => {
            navigate('/login', { state: { verified: true } });
          }, 1500);
        }
      } else {
        setMessage({ text: response.data.message || ' ', type: 'error' });
      }
    } catch (error) {
      console.error('   :', error);
      setMessage({ text: '    .', type: 'error' });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="email-verification-container">
      <h2> </h2>
      <p>      .</p>

      {message && (
        <div className={`message ${message.type}`}>
          {message.text}
        </div>
      )}
      
      <div className="verification-form">
        <div className="input-group">
          <label htmlFor="email"></label>
          <input
            id="email"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="  "
            disabled={codeRequested || isLoading}
          />
        </div>

        {!codeRequested ? (
          <button 
            className="request-button" 
            onClick={requestVerificationCode} 
            disabled={isLoading}
          >
            {isLoading ? ' ...' : ''}
          </button>
        ) : (
          <>
            <div className="input-group">
              <label htmlFor="verificationCode"> </label>
              <input
                id="verificationCode"
                type="text"
                value={verificationCode}
                onChange={(e) => setVerificationCode(e.target.value)}
                placeholder="  6  "
                disabled={isLoading}
              />
            </div>
            <button 
              className="verify-button" 
              onClick={verifyCode} 
              disabled={isLoading}
            >
              {isLoading ? ' ...' : ' '}
            </button>
            <button 
              className="resend-button" 
              onClick={() => {
                setCodeRequested(false);
                requestVerificationCode();
              }} 
              disabled={isLoading}
            >
               
            </button>
          </>
        )}
      </div>
      
      <div className="verification-links">
        <a href="/login"> </a>
      </div>
    </div>
  );
};

export default EmailVerification; 