import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { loginWithSocialAuthCode } from '../../api/socialAuthService';

interface DebugInfo {
  code?: string;
  responseError?: string;
  responseStatus?: number;
  responseData?: any;
}

const GoogleCallback: React.FC = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState<boolean>(true);
  const [debugInfo, setDebugInfo] = useState<DebugInfo>({});

  useEffect(() => {
    const processGoogleLogin = async () => {
      // URL   
      const urlParams = new URLSearchParams(window.location.search);
      const code = urlParams.get('code');
      
      console.log('  :', { code: code ? '' : '' });
      setDebugInfo({ code: code ? '' : '' });
      
      //      
      if (!code) {
        console.error('    ');
        setTimeout(() => {
          window.location.href = '/login';
        }, 500);
        return;
      }

      try {
        console.log('    - :', code);
        
        //  API 
        const response = await loginWithSocialAuthCode({
          code: code,
          provider: 'google'
        });

        //   
        console.log('    :', JSON.stringify(response, null, 2));
        console.log('    :', !!response.profileImage);
        
        //          
        console.log('   :', response);
        
        //         ()
        localStorage.removeItem('profileImage');
        
        //   
        if (response.success && response.token) {
          localStorage.setItem('token', response.token);
          sessionStorage.setItem('token', response.token);
          
          if (response.username) {
            localStorage.setItem('username', response.username);
          }
          
          //   URL 
          if (response.profileImage) {
            localStorage.setItem('profileImage', response.profileImage);
            console.log('   :', response.profileImage);
          } else {
            console.log('    .');
          }
        }
        
        //   
        console.log('  :', {
          localStorage: !!localStorage.getItem('token'),
          sessionStorage: !!sessionStorage.getItem('token'),
          profileImage: localStorage.getItem('profileImage')
        });
        
        //        
        setTimeout(() => {
          const redirectUrl = response.redirectUrl || '/main';
          console.log(' URL:', redirectUrl);
          window.location.href = redirectUrl;
        }, 1000);
      } catch (err: any) {
        //     
        console.error('    :', err);
        setTimeout(() => {
          window.location.href = '/main';
        }, 1000);
      } finally {
        setLoading(false);
      }
    };

    processGoogleLogin();
  }, [navigate]);

  //    -     
  return (
    <div 
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        flexDirection: 'column',
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
      }}
    >
      <div style={{ 
        backgroundColor: 'white', 
        borderRadius: '8px', 
        padding: '24px',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)', 
        textAlign: 'center',
        maxWidth: '400px' 
      }}>
        <div style={{ 
          fontSize: '20px', 
          fontWeight: 'bold', 
          marginBottom: '16px',
          color: '#333'
        }}>
             ...
        </div>
        <div style={{ 
          fontSize: '14px', 
          color: '#666', 
          marginBottom: '20px' 
        }}>
           .    .
        </div>
        <div style={{
          width: '50px',
          height: '50px',
          border: '5px solid #f3f3f3',
          borderTop: '5px solid #3498db',
          borderRadius: '50%',
          margin: '0 auto',
          animation: 'spin 1s linear infinite'
        }} />
        <style>
          {`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
          `}
        </style>
      </div>
    </div>
  );
};

export default GoogleCallback; 