import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import KakaoLoginButton from "../../../components/KakaoLoginButton";
import GoogleLoginButton from "../../../components/GoogleLoginButton";
import NaverLoginButton from "../../../components/NaverLoginButton";
import loginApi from "../api";
import "./styles.css";

//   
const validateLogin = (username: string, password: string): string | null => {
    if (!username.trim()) return " .";
    if (!password) return " .";
    return null;
};

const Login = () => {
    const navigate = useNavigate();
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [isLoading, setIsLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        const validationError = validateLogin(username, password);
        if (validationError) {
            setErrorMessage(validationError);
            return;
        }

        try {
            setIsLoading(true);
            setErrorMessage("");

            const response = await loginApi({ username, password });

            if (response.success) {
                //     
                clearPreviousUserData();
                
                //      
                if (response.username) {
                    localStorage.setItem('username', response.username);
                    console.log(' :    ', response.username);
                } else {
                    //         
                    localStorage.setItem('username', username);
                    console.log(' :    ', username);
                }
                
                navigate("/main");
            } else {
                setErrorMessage(response.message || " .");
            }
        } catch (error) {
            setErrorMessage("    .");
            console.error(" :", error);
        } finally {
            setIsLoading(false);
        }
    };

    //     
    const clearPreviousUserData = () => {
        console.log('    ...');
        
        //    
        localStorage.removeItem('profileImage');
        
        //   
        localStorage.removeItem('userProfileImage');
        localStorage.removeItem('userNickname');
        localStorage.removeItem('signupProfileImage');
        
        //   
        localStorage.removeItem('currentWorkspaceId');
        
        //    (profileImage_*, nickname_* )
        Object.keys(localStorage).forEach(key => {
            if (key.includes('profileImage_') || 
                key.includes('nickname_') || 
                key.includes('workspace_') ||
                key.includes('_owner') || 
                key.includes('_user') ||
                key.includes('invited_by_')) {
                console.log(`: ${key}`);
                localStorage.removeItem(key);
            }
        });
    };

    return (
        <div className="login-container">
            <div className="login-card">
                <h2></h2>

                {errorMessage && (
                    <div className="login-error">{errorMessage}</div>
                )}

                <form onSubmit={handleSubmit} className="login-form">
                    <div>
                        <label htmlFor="username"></label>
                        <input
                            id="username"
                            type="text"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            placeholder=" "
                        />
                    </div>

                    <div>
                        <label htmlFor="password"></label>
                        <input
                            id="password"
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder=" "
                        />
                    </div>

                    <button type="submit" className="login-button" disabled={isLoading}>
                        {isLoading ? " ..." : ""}
                    </button>
                </form>

                <div className="login-divider"></div>

                <KakaoLoginButton />
                <div style={{ margin: '10px 0' }}></div>
                <GoogleLoginButton />
                <div style={{ margin: '10px 0' }}></div>
                <NaverLoginButton />

                <div className="login-links" style={{ marginTop: '30px' }}>
                    <Link to="/signup"></Link>
                </div>
            </div>
        </div>
    );
};

export default Login;
