"use client";

import React, { useState, useEffect, useRef, KeyboardEvent } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import "./styles.css";
import chatImage from "../../../../shared/image/chat.png";
import { getWorkspaceMembers, getWorkspace, getWorkspaceOnlineMembers, getUserOnlineStatus } from "../../../../api/workspaceService";
import { getCurrentUser, getUsernameFromStorage } from "../../../../api/authService";
import { 
  connectToChat, 
  subscribeToChatRoom, 
  sendChatMessage, 
  sendJoinMessage,
  sendTypingStatus,
  getChatHistory,
  MessageType,
  ChatMessage as ChatMessageType
} from "../../../../api/chatService";

//    (   )
type Member = {
    id: number;
    username: string;
    email: string;
    nickname?: string;
    profileImageUrl?: string;
    role: string;
    status: "online" | "offline"; // /  
};

//   
type Message = {
    id: string | number;
    sender: string;
    senderProfileUrl?: string;
    senderId?: number;
    time: string;
    content: string;
    type?: string;
};

type GroupedMessage = {
    id: string | number;
    sender: string;
    time: string;
    contents: string[];
    senderProfileUrl?: string;
};

//     
interface OnlineStatus {
    [key: string | number]: boolean;
}

const WorkspaceChat: React.FC = () => {
    const { id: workspaceIdParam } = useParams<{ id: string }>();
    const workspaceId = workspaceIdParam ? parseInt(workspaceIdParam) : null;
    const [members, setMembers] = useState<Member[]>([]);
    const [workspaceName, setWorkspaceName] = useState<string>("");
    const [isLoading, setIsLoading] = useState<boolean>(true);
    const [currentUser, setCurrentUser] = useState<any>(null);
    const [onlineStatus, setOnlineStatus] = useState<OnlineStatus>({});
    const [onlineMemberIds, setOnlineMemberIds] = useState<number[]>([]);
    const [forceUpdate, setForceUpdate] = useState<number>(0); //    
    
    //    
    const [messages, setMessages] = useState<Message[]>([]);
    const [newMessage, setNewMessage] = useState<string>("");
    const [isConnected, setIsConnected] = useState<boolean>(false);
    const [isTyping, setIsTyping] = useState<boolean>(false);
    const [typingUsers, setTypingUsers] = useState<Set<string>>(new Set());
    const [connectionStatus, setConnectionStatus] = useState<'connected' | 'connecting' | 'disconnected'>('connecting');
    const [messageQueue, setMessageQueue] = useState<ChatMessageType[]>([]);
    const typingTimeoutRef = useRef<any>(null);
    const reconnectIntervalRef = useRef<any>(null);
    
    const navigate = useNavigate();
    const chatBodyRef = useRef<HTMLDivElement>(null);
    const inputRef = useRef<HTMLInputElement>(null);

    //     
    useEffect(() => {
        if (workspaceId) {
            loadWorkspaceData(workspaceId);
            loadCurrentUser();
            //       
            const intervalId = setInterval(() => {
                fetchOnlineStatus();
                //     UI  
                setForceUpdate(prev => prev + 1);
            }, 10000); // 10 
            
            return () => {
                clearInterval(intervalId);
            };
        } else {
            navigate('/main');
        }
    }, [workspaceId]);
    
    //        
    useEffect(() => {
        if (currentUser && members.length > 0) {
            console.log('      ');
            fetchOnlineStatus();
        }
    }, [currentUser, members.length]);

    //    
    const loadCurrentUser = async () => {
        try {
            const user = await getCurrentUser();
            console.log('  :', user);
            
            //     
            const usernameFromStorage = getUsernameFromStorage();
            console.log('localStorage   :', usernameFromStorage);
            
            if (user) {
                setCurrentUser({
                    ...user,
                    usernameFromStorage  //     
                });
            }
        } catch (error) {
            console.error('    :', error);
        }
    };

    //   
    const loadWorkspaceData = async (id: number) => {
        try {
            setIsLoading(true);
            
            //   
            const workspace = await getWorkspace(id);
            setWorkspaceName(workspace.name);
            console.log(' :', workspace);
            
            //    
            const membersData = await getWorkspaceMembers(id);
            console.log('  :', membersData);
            
            //     URL 
            membersData.forEach(member => {
                console.log(` ${member.username || member.email}  :`, member.profileImageUrl);
            });
            
            //    (  )
            const initialMembers = membersData.map(member => ({
                ...member,
                status: "offline" as "online" | "offline"
            }));
            
            setMembers(initialMembers);
            
            //   
            await fetchOnlineStatus();
            
            setIsLoading(false);
        } catch (error) {
            console.error('    :', error);
            navigate('/main');
        }
    };

    //    -    
    const fetchOnlineStatus = async () => {
        try {
            if (!workspaceId) return;
            
            //    
            const currentUsername = getUsernameFromStorage();
            console.log('  :', currentUsername);
            
            //   ID,    
            console.log('   :');
            members.forEach(member => {
                console.log(` ID: ${member.id}, : ${member.username}, : ${member.email}, : ${member.role}`);
            });
            
            //    ID  
            const onlineMembers = await getWorkspaceOnlineMembers(workspaceId);
            console.log('    ID :', onlineMembers);
            setOnlineMemberIds(onlineMembers);
            
            //   
            const updatedStatus: OnlineStatus = {};
            
            //     
            for (const member of members) {
                //     (    )
                const isCurrentUser = currentUsername && (member.username === currentUsername);
                
                // ID  
                if (!member.id) {
                    console.error(` ${member.username || member.email} ID  `);
                    //   ,   
                    if (member.username) {
                        updatedStatus[member.username] = Boolean(isCurrentUser);
                    }
                    continue;
                }
                
                //      ( ID    )
                const isOnlineFromServer = onlineMembers.includes(member.id);
                console.log(` ${member.username}(ID: ${member.id})   :`, isOnlineFromServer);
                
                //      ,      
                const isOnline = isCurrentUser || isOnlineFromServer;
                
                // member.id  ID  ,   username  
                const key = member.id.toString();
                updatedStatus[key] = isOnline;
                
                //   
                console.log(` ${member.username || member.email} (ID: ${member.id})   :`, 
                    isCurrentUser ? ' ( )' : isOnline ? '' : '');
            }
            
            console.log('  :', updatedStatus);
            setOnlineStatus(updatedStatus);
            
            //   
            setMembers(prevMembers => 
                prevMembers.map(member => {
                    //  ID  username  
                    const key = member.id ? member.id.toString() : (member.username || '');
                    const isOnline = !!updatedStatus[key];
                    
                    //      
                    const isCurrentUser = currentUsername && (member.username === currentUsername);
                    const newStatus = (isCurrentUser || isOnline) ? "online" : "offline";
                    
                    return {
                        ...member,
                        status: newStatus
                    };
                })
            );
            
        } catch (error) {
            console.error('    :', error);
            
            //     :    
            if (currentUser) {
                handleFallbackOnlineStatus();
            }
        }
    };
    
    //        (  )
    const handleFallbackOnlineStatus = () => {
        console.log('    -    ');
        
        if (!currentUser) return;
        
        const updatedStatus: OnlineStatus = {};
        let currentUserFound = false;
        
        members.forEach(member => {
            //     
            const isCurrentUser = 
                member.id === currentUser.id || 
                member.username === currentUser.username ||
                member.email === currentUser.email ||
                (currentUser.nickname && member.nickname === currentUser.nickname) ||
                member.username === currentUser.usernameFromStorage;
            
            if (isCurrentUser) {
                console.log('  :', member.username);
                updatedStatus[member.id] = true;
                currentUserFound = true;
            } else {
                updatedStatus[member.id] = false; //   
            }
        });
        
        //   
        setMembers(prevMembers => 
            prevMembers.map(member => ({
                ...member,
                status: updatedStatus[member.id] ? "online" : "offline"
            }))
        );
    };

    //    useEffect
    useEffect(() => {
        if (workspaceId && currentUser) {
            setConnectionStatus('connecting');
            
            //     
            const handleConnectionStatus = (status: 'connected' | 'connecting' | 'disconnected') => {
                setConnectionStatus(status);
                setIsConnected(status === 'connected');
                
                //       
                if (status === 'connected' && messageQueue.length > 0) {
                    console.log(`   ${messageQueue.length}   `);
                    
                    //     
                    messageQueue.forEach(msg => {
                        sendChatMessage(msg)
                            .then(() => {
                                console.log('    :', msg);
                                //     
                                setMessageQueue(prev => prev.filter(m => m !== msg));
                            })
                            .catch(error => {
                                console.error('    :', error);
                            });
                    });
                }
            };
            
            //   
            const client = connectToChat(() => {
                handleConnectionStatus('connected');
                console.log('  .');
                
                //   
                subscribeToChatRoom(workspaceId, handleChatMessageReceived);
                
                //   
                if (currentUser.id && currentUser.username) {
                    sendJoinMessage(
                        workspaceId, 
                        currentUser.id,
                        currentUser.username
                    ).catch(error => console.error('   :', error));
                }
                
                //   
                loadChatHistory();
            });
            
            //       
            if (client) {
                client.onStompError = () => {
                    handleConnectionStatus('disconnected');
                    console.error('STOMP  ');
                };
                
                client.onWebSocketClose = () => {
                    handleConnectionStatus('disconnected');
                    console.log('WebSocket  .   ...');
                    
                    //      
                    if (!reconnectIntervalRef.current) {
                        reconnectIntervalRef.current = setInterval(() => {
                            if (connectionStatus !== 'connected') {
                                handleConnectionStatus('connecting');
                            } else {
                                clearInterval(reconnectIntervalRef.current);
                                reconnectIntervalRef.current = null;
                            }
                        }, 5000); // 5   
                    }
                };
            }
            
            //      
            return () => {
                if (reconnectIntervalRef.current) {
                    clearInterval(reconnectIntervalRef.current);
                }
            };
        }
    }, [workspaceId, currentUser]);

    //   
    const loadChatHistory = async () => {
        if (!workspaceId) return;
        
        try {
            const history = await getChatHistory(workspaceId);
            
            //      
            const convertedMessages = history.map((msg: ChatMessageType) => ({
                id: msg.id || '',
                sender: msg.senderName,
                senderId: msg.senderId,
                senderProfileUrl: msg.senderProfileUrl,
                time: formatTimestamp(msg.timestamp),
                content: msg.content,
                type: msg.type
            }));
            
            setMessages(convertedMessages);
            
        } catch (error) {
            console.error('    :', error);
        }
    };

    //   
    const handleChatMessageReceived = (message: ChatMessageType) => {
        //    
        switch (message.type) {
            case MessageType.TYPING:
                handleTypingStatus(message);
                break;
                
            case MessageType.JOIN:
            case MessageType.LEAVE:
            case MessageType.CHAT:
                const newMsg: Message = {
                    id: message.id || Date.now().toString(),
                    sender: message.senderName,
                    senderId: message.senderId,
                    senderProfileUrl: message.senderProfileUrl,
                    time: formatTimestamp(message.timestamp),
                    content: message.content,
                    type: message.type
                };
                
                setMessages(prev => [...prev, newMsg]);
                break;
        }
    };

    //   
    const handleTypingStatus = (message: ChatMessageType) => {
        const username = message.senderName;
        const isTypingNow = message.content === 'typing';
        
        if (isTypingNow) {
            //    
            setTypingUsers(prev => {
                const newSet = new Set(prev);
                newSet.add(username);
                return newSet;
            });
        } else {
            //    
            setTypingUsers(prev => {
                const newSet = new Set(prev);
                newSet.delete(username);
                return newSet;
            });
        }
    };

    //    
    const formatTimestamp = (timestamp: any): string => {
        if (!timestamp) return new Date().toLocaleTimeString();
        
        let date;
        if (typeof timestamp === 'string') {
            date = new Date(timestamp);
        } else {
            date = timestamp;
        }
        
        //    ,     
        const today = new Date();
        if (date.toDateString() === today.toDateString()) {
            return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
        } else {
            return date.toLocaleDateString() + ' ' + date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
        }
    };

    //   
    const handleSendMessage = () => {
        if (!newMessage.trim() || !workspaceId || !currentUser) return;
        
        const message: ChatMessageType = {
            chatRoomId: workspaceId,
            senderId: currentUser.id,
            senderName: currentUser.username,
            content: newMessage.trim(),
            type: MessageType.CHAT,
            senderProfileUrl: currentUser.profileImageUrl
        };
        
        //    
        if (connectionStatus === 'connected') {
            //    
            sendChatMessage(message)
                .then(() => {
                    //      
                    setNewMessage('');
                    
                    //     
                    if (isTyping) {
                        setIsTyping(false);
                        sendTypingStatus(
                            workspaceId, 
                            currentUser.id, 
                            currentUser.username, 
                            false
                        );
                    }
                    
                    //  
                    if (inputRef.current) {
                        inputRef.current.focus();
                    }
                })
                .catch(error => {
                    console.error('   :', error);
                    
                    //      
                    setMessageQueue(prev => [...prev, message]);
                    
                    //  
                    setNewMessage('');
                    
                    //   
                    setConnectionStatus('disconnected');
                });
        } else {
            //     
            console.log('  ,   :', message);
            setMessageQueue(prev => [...prev, message]);
            
            //  
            setNewMessage('');
            
            //   
            const tempMsg: Message = {
                id: `temp-${Date.now()}`,
                sender: currentUser.username,
                senderId: currentUser.id,
                senderProfileUrl: currentUser.profileImageUrl,
                time: formatTimestamp(new Date()),
                content: newMessage.trim() + ' ( ...)',
                type: 'TEMP'
            };
            
            setMessages(prev => [...prev, tempMsg]);
        }
    };

    //   
    const handleKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
        // Enter   
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleSendMessage();
        }
    };

    //   
    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setNewMessage(value);
        
        //    
        if (workspaceId && currentUser) {
            //       
            if (!isTyping && value.trim()) {
                setIsTyping(true);
                sendTypingStatus(
                    workspaceId, 
                    currentUser.id, 
                    currentUser.username, 
                    true
                );
            }
            
            //    
            if (typingTimeoutRef.current) {
                clearTimeout(typingTimeoutRef.current);
            }
            
            // 1    
            typingTimeoutRef.current = setTimeout(() => {
                if (isTyping) {
                    setIsTyping(false);
                    sendTypingStatus(
                        workspaceId, 
                        currentUser.id, 
                        currentUser.username, 
                        false
                    );
                }
            }, 1000);
        }
    };

    //    
    const groupConsecutiveMessages = (messages: Message[]): GroupedMessage[] => {
        const grouped: GroupedMessage[] = [];
        let currentGroup: GroupedMessage | null = null;

        for (let i = 0; i < messages.length; i++) {
            const msg = messages[i];
            const isSameSender = currentGroup && msg.sender === currentGroup.sender;
            const isSameTime = currentGroup && isSameTimeFrame(msg.time, currentGroup.time);

            if (isSameSender && isSameTime && currentGroup) {
                currentGroup.contents.push(msg.content);
            } else {
                currentGroup = {
                    id: msg.id,
                    sender: msg.sender,
                    time: msg.time,
                    contents: [msg.content],
                    senderProfileUrl: msg.senderProfileUrl
                };
                grouped.push(currentGroup);
            }
        }

        return grouped;
    };

    //   
    const isSameTimeFrame = (time1: string, time2: string): boolean => {
        // 5     
        const date1 = new Date(time1);
        const date2 = new Date(time2);
        
        //    
        if (isNaN(date1.getTime()) || isNaN(date2.getTime())) {
            return time1 === time2;
        }
        
        return Math.abs(date1.getTime() - date2.getTime()) < 5 * 60 * 1000;
    };

    //  
    const groupedMessages = groupConsecutiveMessages(messages);

    //   
    useEffect(() => {
        if (chatBodyRef.current) {
            chatBodyRef.current.scrollTop = chatBodyRef.current.scrollHeight;
        }
    }, [groupedMessages]);

    //   URL 
    const getProfileImageUrl = (member: Member) => {
        console.log('  URL :', member.username, member.profileImageUrl);
        
        if (member.profileImageUrl) {
            //  URL  URL   
            if (member.profileImageUrl.startsWith('http')) {
                return member.profileImageUrl;
            }
            
            //    API  URL 
            // process.env      
            const baseApiUrl = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8080';
            
            // URL     
            let profilePath = member.profileImageUrl;
            if (profilePath.startsWith('/') && baseApiUrl.endsWith('/')) {
                profilePath = profilePath.substring(1);
            } else if (!profilePath.startsWith('/') && !baseApiUrl.endsWith('/')) {
                profilePath = '/' + profilePath;
            }
            
            const fullUrl = baseApiUrl + profilePath;
            console.log('  URL:', fullUrl);
            return fullUrl;
        }
        
        console.log('  ');
        // profileImageUrl     
        return chatImage;
    };
    
    //    
    const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
        e.currentTarget.src = chatImage; //   
    };

    //   
    const getUserInitial = (member: Member) => {
        //     
        const name = member.nickname || member.username || member.email || '';
        return name.charAt(0).toUpperCase();
    };

    //     
    const getSenderImageUrl = (group: GroupedMessage) => {
        //    
        const senderMember = members.find(m => m.username === group.sender || m.nickname === group.sender);
        
        if (senderMember?.profileImageUrl) {
            return getProfileImageUrl(senderMember);
        }
        
        return chatImage;
    };

    //    useEffect
    useEffect(() => {
        if (forceUpdate > 0) {
            console.log('  ', forceUpdate);
        }
    }, [forceUpdate]);

    if (isLoading) {
        return <div className="loading"> ...</div>;
    }

    return (
        <div className="workspaceChat-container">
            {/*  */}
            <div className="workspaceChat-sidebar">
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
                <div className="workspaceChat-sidebar-menu"></div>
            </div>

            {/*    */}
            <div className="workspaceChat-memberList">
                {["online", "offline"].map((status) => (
                    <div className="workspaceChat-statusGroup" key={status}>
                        <div className="workspaceChat-statusHeader">
                            {status === "online" 
                                ? ` - ${members.filter(m => m.status === "online").length}` 
                                : ` - ${members.filter(m => m.status === "offline").length}`}
                        </div>
                        {members
                            .filter((member) => member.status === status)
                            .map((member) => (
                                <div className="workspaceChat-member" key={member.id}>
                                    {member.profileImageUrl ? (
                                        <img 
                                            src={getProfileImageUrl(member)} 
                                            className="workspaceChat-avatar" 
                                            alt="profile"
                                            onError={handleImageError}
                                        />
                                    ) : (
                                        <div className="workspaceChat-avatar workspaceChat-avatar-initial">
                                            {getUserInitial(member)}
                                        </div>
                                    )}
                                    <span
                                        className={`workspaceChat-status-dot ${status === "online" ? "green" : "red"}`}>
                                    </span>
                                    <span className="workspaceChat-name">
                                        {member.nickname || member.username}
                                    </span>
                                </div>
                            ))}
                    </div>
                ))}
            </div>

            {/*   */}
            <div className="workspaceChat-chat-container">
                <div className="workspaceChat-chat-header">
                    {workspaceName}
                    {connectionStatus !== 'connected' && (
                        <div className="workspaceChat-connection-status">
                            {connectionStatus === 'connecting' ? ' ...' : ' '}
                        </div>
                    )}
                </div>
                <div className="workspaceChat-chat-body" ref={chatBodyRef}>
                    {/*      */}
                    {connectionStatus !== 'connected' && (
                        <div className="workspaceChat-connection-banner">
                            {connectionStatus === 'connecting' 
                                ? '   ...' 
                                : '   .  ...'
                            }
                        </div>
                    )}
                    
                    {/*     */}
                    {groupedMessages.length === 0 ? (
                        <div className="workspaceChat-empty-chat">
                            <p>   .   !</p>
                        </div>
                    ) : (
                        <div className="workspaceChat-chat-message">
                            {groupedMessages.map((group) => (
                                <div key={group.id} className={`workspaceChat-message-block ${group.contents.some(c => c.includes(' ')) ? 'pending' : ''}`}>
                                    <div className={"workspaceChat-message-senderImage"}>
                                        <img 
                                            src={getSenderImageUrl(group)}
                                            alt="sender"
                                            onError={handleImageError}
                                        /> 
                                    </div>
                                    <div>
                                        <div className="workspaceChat-message-header">
                                            <div className="sender-time">
                                                <strong>{group.sender}</strong>
                                                <span className="time">{group.time}</span>
                                            </div>
                                        </div>
                                        <div className="workspaceChat-message-body">
                                            {group.contents.map((line, i) => (
                                                <div key={i}>{line}</div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                    
                    {/*      */}
                    {messageQueue.length > 0 && (
                        <div className="workspaceChat-queue-info">
                            {messageQueue.length}    .
                        </div>
                    )}
                    
                    {/*   */}
                    {typingUsers.size > 0 && (
                        <div className="workspaceChat-typing">
                            {Array.from(typingUsers).join(', ')}
                            {typingUsers.size === 1 ? '  ...' : '  ...'}
                        </div>
                    )}
                </div>
                <div className="workspaceChat-inputBox">
                    <input 
                        type="text" 
                        placeholder={connectionStatus === 'connected' 
                            ? " ..." 
                            : connectionStatus === 'connecting' 
                                ? " ..." 
                                : " ..."}
                        value={newMessage}
                        onChange={handleInputChange}
                        onKeyDown={handleKeyDown}
                        ref={inputRef}
                        disabled={connectionStatus === 'connecting'}
                    />
                    <div className="workspaceChat-button"></div>
                    <div className="workspaceChat-button"></div>
                    <div 
                        className={`workspaceChat-button sendButton ${connectionStatus !== 'connected' ? 'disabled' : ''}`}
                        onClick={handleSendMessage}
                    >
                        {messageQueue.length > 0 ? ` (${messageQueue.length})` : ''}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default WorkspaceChat;
