"use client";

import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { getWorkspace } from '../../../api/workspaceService';
import './styles.css';
import ChatUI from './ui';

const WorkspaceChat = () => {
    const navigate = useNavigate();
    const params = useParams();
    const [workspaceId, setWorkspaceId] = useState<number | null>(null);
    const [workspaceName, setWorkspaceName] = useState('');
    const [loading, setLoading] = useState(true);

    // URL  ID 
    useEffect(() => {
        // URL ID  
        if (params.id && !isNaN(Number(params.id))) {
            const extractedId = Number(params.id);
            setWorkspaceId(extractedId);
            
            //   
            loadWorkspaceInfo(extractedId);
        } else {
            // URL  ID    
            console.error(' ID URL   ');
            
            //   
            navigate('/main');
        }
    }, [params.id, navigate]);

    //   
    const loadWorkspaceInfo = async (id: number) => {
        try {
            setLoading(true);
            const workspace = await getWorkspace(id);
            setWorkspaceName(workspace.name);
            setLoading(false);
        } catch (error) {
            console.error('    :', error);
            //     
            navigate('/main');
        }
    };

    if (loading) {
        return <div className="loading"> ...</div>;
    }

    return (
        <div className="workspace-chat-container">
            <ChatUI />
        </div>
    );
};

export default WorkspaceChat;